import {Button, Divider, Input, Select} from 'antd';
import {UserOutlined,LockOutlined} from '@ant-design/icons';
import logo_src from '../../assert/logo.png';
import './index.css'
import {Link} from 'react-router-dom'
const { Option } = Select;

const Login = () => {

    return (
                <div className="container">
                    <img className="logo" src={logo_src} alt="logo"/>
                    <div id="login-form">
                        {/*<div className="login-header">账号密码登录</div>*/}
                        <Divider className="login-header">账号密码登录</Divider>

                        {/*<div className="c-hr">--------</div>*/}
                        {/*<hr/>*/}
                        <br/>
                        <div id="dock">

                            <Input
                                placeholder="用户名"
                                prefix={<UserOutlined style={{ fontSize: '16px', color: '#08c' }}/>}
                                maxLength={15}
                            />
                            <br/>
                            <br/>
                            <Input.Password
                                placeholder="密码"
                                prefix={
                                    <LockOutlined style={{ fontSize: '16px', color: '#08c' }}/>
                                }
                            />
                            <br/>
                            <br/>
                            <Select placeholder="请选择登录身份"  style={{width:"19em"}} aria-required="true">
                                <Option value="jack">捐衣服</Option>
                                <Option value="lucy">设计</Option>
                                <Option value="Yiminghe">购买</Option>
                            </Select>
                            <br/>
                            <br/>
                            <Link to="/home"><Button type="primary" style={{width:"19em"}}>登录</Button></Link>
                        </div>
                    </div>
                </div>
    );
};
export default Login;